.cat-info {
  display: flex;
}

.image {
  margin-right: 10px;
}

.loader {
  color: #ff3d00;
  letter-spacing: 2px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 30px;
  font-weight: bold;
  display: block;
  position: relative;
}

.loader:after {
  content: "Loading...";
  color: #0a0a0a;
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  animation: animloader 10s ease-in infinite;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}

@keyframes animloader {
  0% {
    width: 0%;
  }

  100% {
    width: 100%;
  }
}

.is-hidden {
  display: none;
}

.breed-select {
  margin-bottom: 20px;
}

